}

.menu--primary {
	float: right;
}
.menu--primary .menu__title {
	margin: 0 -1.125rem 0 0;
	width: 6rem;
	height: 6rem;
}
.menu--primary .menu__button {
	width: 100%;
	height: 100%;
	padding: 0;
}
.menu--primary .menu__icon {
	font-size: 1.5rem;
}
.menu--primary .menu__items,
.menu--primary .menu__sub-menu {
	list-style-type: none;
	margin: 0;
	padding: 0;
}
.menu--primary .menu__link {
	display: block;
	color: #ccc;
}
.menu--primary .menu__link:hover, .menu--primary .menu__link:focus {
	color: #fff;
}
.menu--primary .has-children > .menu__link::after {
	content: "";
	vertical-align: bottom;
	margin-left: 0.5625rem;
}
.menu--primary .menu__sub-menu {
	font-size: 0.95em;
	z-index: 999;
}
.menu--primary .menu__sub-menu .menu__link {
	color: rgba(255, 255, 255, 0.75);
}

@media (min-width: 1025px) {
	.menu--primary .menu__title {
